html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

.container {
  height: 35rem;
  width: 100%;
  background-color: #000;
  position: relative;
  background-image: url("../assets/bg-main.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  overflow: hidden;
}
.container__text {
  position: absolute;
  height: 10rem;
  left: 50%;
  transform: translate(-50%, 0);
}
.container__shoe {
  position: absolute;
  height: 12rem;
  bottom: 2.5rem;
  left: 50%;
  transform: translate(-50%, 0);
}